import React, { useState } from "react";
import { makeStyles, Button, Input, Label } from "@fluentui/react-components";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
// import { Login } from "../Services/LoginApi";
// import Logo from "../../../../assets/Logo.png"
import Loader from "../../components/Loader";
const useStyles = makeStyles({
  root: {
    display: "flex",
    flexDirection: "column",
    gap: "10px",

    maxWidth: "400px",
    "> div": { display: "flex", flexDirection: "column", gap: "2px" },
  },
});

const SignIn = () => {
  const navigate = useNavigate();
  const styles = useStyles();
  const [loader, setLoader] = useState(false);

  const {
    register,
    formState: { errors },
    handleSubmit,
  } = useForm({
    mode: "onSubmit",
  });

  const onSubmitHandler = async (data) => {
    setLoader(true); // Start loader
    console.log(data);
    window.localStorage.setItem("tenantId", "7344384A-A2F4-4FC4-A382-315FCB421A72");
    setTimeout(() => {
      setLoader(false); // Stop loader
      navigate("/Suggestions");
    }, 3000);
  };

  return (
    <>
      {loader && <Loader />}
      <div className="flex flex-col justify-center align-center h-[80vh] p-7 pt-0 ">
        <div className=" flex justify-center -mt-16 mb-10">{/* <img src={Logo} alt="Logo" className="w-48" /> */}</div>

        <h1 className="text-2xl text-white font-bold">Einloggen</h1>
        <p className="text-xs text-white mb-5">
          Logge dich in Dein aicall account <br /> ein um Email-Al zu nutzen
        </p>
        <form onSubmit={handleSubmit(onSubmitHandler)} noValidate autoComplete="off" className={styles.root}>
          <div className="mb-3">
            <input
              className="appearance-none bg-white rounded-lg w-full py-3 px-3 text-gray-700 focus:outline-none focus:border-none"
              id="email"
              type="email"
              placeholder="E-Mail Adresse"
              isInvalid={!!errors.email}
              {...register("email", {
                required: "Email is required!",
                // pattern: {
                //   value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                //   message: "Invalid email format!",
                // },
              })}
            />
            <p className="text-red-700 text-end">{errors.email?.message}</p>
          </div>
          <div className="mb-4">
            <input
              className="appearance-none bg-white rounded-lg w-full py-3 px-3 text-gray-700 focus:outline-none focus:border-none"
              id="passwort"
              type="password"
              placeholder="Passwort"
              isInvalid={!!errors.passwort}
              {...register("passwort", {
                required: "Passwort is required!",
              })}
            />
            <p className="text-red-700 text-end">{errors.passwort?.message}</p>
          </div>
          <div className="mt-4 flex justify-center align-center">
            <button
              type="submit"
              appearance="primary"
              className="appearance-none cursor-pointer bg-[#e1ff64] font-bold text-[#5b4cb7] rounded-lg w-full py-3 px-3 text-gray-700"
            >
              Log-In
            </button>
          </div>
        </form>
      </div>
    </>
  );
};

export default SignIn;
