import React, { useState, useEffect } from "react";
import "react-chat-elements/dist/main.css";
import { generateEmailResponse } from "../../../ApiServices/generate-email-response";
import { improveEmailResponse } from "../../../ApiServices/improve-email-response";
import { Button } from "@fluentui/react-components";
import { MoveRight } from "lucide-react";
import "./suggestions.css";
import Loader from "../../components/Loader";
import { insertBodyContent } from "../../../Utilities/insert-email-body";

const Suggestions = () => {
  const [inputText, setInputText] = useState("");
  const [response, setResponse] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [selectedButton, setSelectedButton] = useState("Antwort 1");
  const [loader, setLoader] = useState(false);
  const [previousEmail, setPreviousEmail] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      setLoader(true);
      try {
        const res = await generateEmailResponse();
        setResponse(res.responseData.response);
        setPreviousEmail(res.data);
        setLoader(false);
      } catch (error) {
        console.error("Error generating email response:", error);
        setLoader(false);
      }
    };
    fetchData();
  }, []);

  const handleChangeResponse = (index, buttonText) => {
    setCurrentIndex(index);
    setSelectedButton(buttonText);
  };

  // const handleSend = () => {
  //   console.log("Button ID:", currentIndex + 1);
  //   console.log("Button:", selectedButton);
  //   console.log("Response:", response[currentIndex]);

  //   sendEmail();
  // };

  const handleSendNow = async () => {
    console.log("Button ID:", currentIndex + 1);
    console.log("Button:", selectedButton);
    console.log("Response:", response[currentIndex]);
    const data = {
      tenantid: window.localStorage.getItem("tenantId"),
      top_k: previousEmail.top_k,
      subject: previousEmail.subject,
      body: previousEmail.body,
      sender: previousEmail.sender,
      recipient: previousEmail.recipient,
      latestmail: response[currentIndex],
      imporve: inputText,
    };
    setLoader(true);
    const res = await improveEmailResponse(data);
    console.log(res);

    setResponse(res.response);
    setLoader(false);
  };
  const onInsert = () => {
    insertBodyContent(response[currentIndex]);
  };

  return (
    <>
      {loader && <Loader />}
      <div className="p-1 rounded-lg mt-2 overflow-x-hidden overflow-y-auto">
        <div className="flex gap-x-2 ms-3 me-3 items-center">
          {["Antwort 1", "Antwort 2", "Antwort 3"].map((buttonText, index) => (
            <button
              key={index}
              className={`w-full cursor-pointer text-xs font-bold py-2 px-4 rounded-tr rounded-tl focus:outline-none focus:shadow-outline text-[#5b4cb7] ${selectedButton === buttonText ? "bg-[#c3e88d]" : "bg-[#e1ff64] hover:bg-[#e1ff64bf]"}`}
              type="button"
              onClick={() => handleChangeResponse(index, buttonText)}
            >
              {buttonText}
            </button>
          ))}
        </div>
        <div className="bg-white rounded-lg ms-2 me-2">
          <div className="h-[55vh] bg-white overflow-auto p-2">
            <p>{response[currentIndex] || "Lade Antwort..."}</p>
          </div>
          <div className="flex">
            <input
              value={inputText}
              onChange={(e) => setInputText(e.target.value)}
              className="appearance-none bg-gray-200 rounded-lg w-full py-2 px-2 m-2 text-gray-700 focus:outline-none focus:border-none"
              type="text"
              placeholder="Antwort anpassen lassen"
            />
            <div className="p-3 bg-grey-500">
              <Button icon={<MoveRight size={26} />} size="large" onClick={handleSendNow} />
            </div>
          </div>
        </div>
        <div className="fixed bottom-1">
          <div className="flex justify-between items-center w-[94vw] gap-2 m-2">
            <button
              className="cursor-pointer bg-[#8774fc] outline outline-white text-white font-bold w-full py-3 px-4 rounded focus:outline-none focus:shadow-outline"
              type="button"
              onClick={onInsert}
            >
              Einsetzen
            </button>
         
          </div>
        </div>
      </div>
    </>
  );
};

export default Suggestions;
