import { config } from "../config";
import { htmlToText } from "html-to-text";
import { fetchConversationMessages } from "./fetchEmailMessage";
export async function generateEmailResponse() {
  let emailData = await fetchConversationMessages();

  const plainTextContent = htmlToText(emailData.Body.Content, {
    wordwrap: 130,
  });
  console.log(emailData);
  return new Promise((resolve, reject) => {
    const data = {
      tenantid: window.localStorage.getItem("tenantId"),
      top_k: 3,
      subject: emailData.Subject,
      body: plainTextContent,
      sender: emailData.From.EmailAddress.Address,
      recipient: emailData.ToRecipients[0].EmailAddress.Address,

      email: "",
    };

    fetch(`${config.API_URL}generate_email_response`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    })
      .then((response) => {
        if (!response.ok) {
          return response.json().then((err) => reject(err));
        }
        return response.json();
      })
      .then((responseData) => resolve({ responseData, data }))
      .catch((error) => reject(error));
  });
}
