import React from "react";
import {
  Menu,
  MenuTrigger,
  MenuList,
  MenuItem,
  MenuPopover,
} from "@fluentui/react-components";
import { HiCog } from "react-icons/hi";
import { IconButton } from "@mui/material";

const Header = () => {
  return (
    <div className="flex justify-between items-center w-full p-5">
      <div className="pt-2">
        <h1 className="text-[17px] text-white font-bold">Mail Assistant</h1>
        <p className="text-[11px] text-white mb-3">Verwandle Deine Emails mit KI</p>
      </div>
      <div>
        <Menu>
          <MenuTrigger disableButtonEnhancement>
            <IconButton
              style={{
                background: "none", // No background
                border: "none", // No border
                boxShadow: "none", // No shadow
                padding: "0", // No extra padding
                minWidth: "auto", // Avoid unnecessary spacing
                transition: "transform 0.2s ease, opacity 0.2s ease", // Smooth hover effect
              }}
              sx={{
                "&:hover": {
                  transform: "scale(1.1)", // Slight zoom on hover
                  opacity: 0.8, // Reduce opacity slightly
                  background: "none", // Ensures no background appears on hover
                },
              }}
            >
              <HiCog style={{ fontSize: "25px", color: "white" }} />
            </IconButton>
          </MenuTrigger>
          <MenuPopover>
            <MenuList>
              <MenuItem>New</MenuItem>
              <MenuItem>New Window</MenuItem>
              <MenuItem disabled>Open File</MenuItem>
              <MenuItem>Open Folder</MenuItem>
            </MenuList>
          </MenuPopover>
        </Menu>
      </div>
    </div>
  );
};

export default Header;
