export async function insertBodyContent(bodyContent) {
  return new Promise((resolve, reject) => {
    Office.context.mailbox.item.body.setAsync(bodyContent, { coercionType: "text" }, (result) => {
      if (result.status === Office.AsyncResultStatus.Succeeded) {
        resolve(true);
      } else {
        reject(result.error.message || "Failed to set body");
      }
    });
  });
}
export function sendEmail() {
  const item = Office.context.mailbox.item;

  if (item && item.itemType === Office.MailboxEnums.ItemType.Message) {
    item.saveAsync(function (result) {
      if (result.status === Office.AsyncResultStatus.Succeeded) {
        // Now submit the email
        item.sendAsync(function (sendResult) {
          if (sendResult.status === Office.AsyncResultStatus.Succeeded) {
            console.log("Email sent successfully!");
          } else {
            console.error("Failed to send email:", sendResult.error.message);
          }
        });
      } else {
        console.error("Failed to save email:", result.error.message);
      }
    });
  } else {
    console.warn("This function only works in compose mode.");
  }
}
